import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router'

import {
  Card,
  FormControl,
  InputLabel,
  ListSubheader,
  MenuItem,
  Select,
  Stack,
  TextField,
  Typography
} from '@mui/material'

import departments from 'src/data/departments'
import { HELPER_TEXT_MESSAGE } from 'src/data/messages'

import { formatLikePhoneNumber } from 'src/utils/formatLikePhoneNumber '

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import { stringLength } from 'src/commons/StringLength'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

interface MemberForm {
  studentId: string
  name: string
  phoneNumber: string
  email: string
  department: string
}

const OneMemberMigrationCard = () => {
  const params = useParams()
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()
  const [member, setMember] = useState<MemberForm>({
    studentId: '',
    name: '',
    phoneNumber: '',
    email: '',
    department: ''
  })

  const NAME_LIMIT = 50

  const isProperStudentIdFormat = (studentId: string): boolean => {
    const regExp = /^[0-9]{4}(KU|[0-9]{2})[0-9]{4}$/
    return regExp.test(studentId)
  }

  const isProperPhoneNumberFormat = (phoneNumber: string): boolean => {
    const regExp = /^01(?:0|1|[6-9])-(?:\d{3}|\d{4})-\d{4}$/
    return regExp.test(phoneNumber)
  }

  const isProperEmailFormat = (email: string) => {
    const regExp = /^[\w-\.]+@[a-zA-Z0-9-]+\.[a-zA-Z0-9-.]+$/
    return regExp.test(email)
  }

  const isProperNameLength = (name: string) => {
    return stringLength(name) < NAME_LIMIT
  }

  const isProperNameFormat = (name: string) => {
    const trimmedName = name.trim()
    const regExp = /^[a-zA-Z0-9ㄱ-ㅎㅏ-ㅣ가-힣 ]+$/
    return regExp.test(name) && trimmedName === name
  }

  const isProperDepartment = (department: string) => {
    return department !== ''
  }

  const handleSubmit = async () => {
    if (!member.studentId || !member.name || !member.phoneNumber) {
      enqueueSnackbar('모든 항목에 대해 작성해 주십시오.', { variant: 'error' })
      return
    }

    if (!isProperStudentIdFormat(member.studentId)) {
      enqueueSnackbar('유효하지 않은 학번 입니다.', { variant: 'error' })
      return
    }

    if (!isProperPhoneNumberFormat(member.phoneNumber)) {
      enqueueSnackbar('유효하지 않은 전화번호 입니다. 010-xxxx-xxxx 형식으로 작성해주세요.', { variant: 'error' })
      return
    }
    if (member.email && !isProperEmailFormat(member.email)) {
      enqueueSnackbar('유효하지 않은 이메일 형식입니다.', { variant: 'error' })
      return
    }

    if (!isProperNameLength(member.name)) {
      enqueueSnackbar('이름은 영문 기준 50자 이내로 작성해 주십시오.', { variant: 'error' })
      return
    }

    if (!isProperNameFormat(member.name)) {
      enqueueSnackbar('유효하지 않은 이름입니다. 특수문자 및 이름 앞뒤 공백문자는 사용할 수 없습니다.', {
        variant: 'error'
      })
      return
    }

    if (!isProperDepartment(member.department)) {
      enqueueSnackbar('올바른 학과를 선택해 주십시오.', { variant: 'error' })
      return
    }

    try {
      const result = await axios.post(
        `${process.env.REACT_APP_APIHOST}/club/${params.id}/member`,
        {
          name: member.name,
          studentId: member.studentId,
          phoneNumber: member.phoneNumber,
          email: member.email || null,
          department: member.department
        },
        {
          headers: { Authorization: authHeader() }
        }
      )

      if (result.data.message !== 'ok') throw new Error()

      enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
      setMember({ studentId: '', name: '', phoneNumber: '', email: '', department: '' })
    } catch (error) {
      if (axios.isAxiosError(error)) {
        enqueueSnackbar(error.response?.data?.message || '등록에 실패했습니다. 다시 시도해 주십시오.', {
          variant: 'error'
        })
      }
    }
  }
  const handlePhoneNumberChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { value } }) => {
    setMember(prevState => ({ ...prevState, phoneNumber: formatLikePhoneNumber(value) }))
  }

  return (
    <Card sx={{ padding: { xs: 2, sm: 2, md: 4 } }}>
      <Typography variant="h3" align="center" mb={3} color={PureLightTheme.colors.primary.main}>
        개별 회원 추가
      </Typography>
      <Stack direction="column" sx={{ margin: 1, marginBottom: 5 }}>
        <TextField
          required
          id="studentId"
          label="학번"
          variant="standard"
          value={member.studentId}
          inputProps={{ maxLength: 10 }}
          onChange={e =>
            setMember(prevState => {
              return { ...prevState, studentId: e.target.value }
            })
          }
          sx={{ margin: 0.3 }}
        />
        <TextField
          required
          id="name"
          label="이름"
          variant="standard"
          value={member.name}
          inputProps={{ maxLength: 50 }}
          onChange={e =>
            setMember(prevState => {
              return { ...prevState, name: e.target.value }
            })
          }
          sx={{ margin: 0.3 }}
        />
        <TextField
          required
          id="phoneNumber"
          label="전화번호"
          helperText={HELPER_TEXT_MESSAGE.PHONE_NUMBER}
          variant="standard"
          value={member.phoneNumber}
          onChange={handlePhoneNumberChange}
          sx={{ margin: 0.3 }}
          inputProps={{ pattern: '010-[0-9]{4}-[0-9]{4}', maxLength: 13 }}
        />
        <TextField
          id="email"
          label="이메일(ex. kim@korea.ac.kr)"
          variant="standard"
          value={member.email}
          onChange={e =>
            setMember(prevState => {
              return { ...prevState, email: e.target.value }
            })
          }
          sx={{ margin: 0.3 }}
        />
        <FormControl sx={{ mt: 2, width: '100%' }} size="small" required>
          <InputLabel htmlFor="department">학과/학부</InputLabel>
          <Select
            id="department"
            name="department"
            label="학과/학부"
            onChange={e =>
              setMember(prevState => {
                return { ...prevState, department: e.target.value as string }
              })
            }
            inputProps={{ pattern: '010-[0-9]{4}-[0-9]{4}' }}
            fullWidth
          >
            {departments &&
              Object.keys(departments).map(college => [
                <ListSubheader sx={{ fontWeight: 'bold' }}>{college}</ListSubheader>,
                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore
                departments[college] &&
                  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                  // @ts-ignore
                  departments[college].map(depart => [
                    <MenuItem value={college === depart ? depart : college + ' ' + depart}>{depart}</MenuItem>
                  ])
              ])}
          </Select>
        </FormControl>
      </Stack>

      <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록" />
    </Card>
  )
}

export default OneMemberMigrationCard
